import React, { Component } from 'react';
import { getSuggestions } from './api';
import { InfoAlert, WarningAlert } from './Alert';

class CitySearch extends Component {
    state = {
        query: "",
        suggestions: [],
        infoText: "",
        warningText: ""
    }

    handleInputChanged = (event) => {
        const value = event.target.value;
        this.setState({ query: value });
        if (!navigator.onLine) {
            this.setState({ warningText: "Ooops... Your app is offline and the events were loaded from cache. Please connect to the internet." });
        } else {
            this.setState({ warningText: "" })
        }
        getSuggestions(value).then(suggestions => {
            this.setState({ suggestions });
            if (value && suggestions.length === 0) {
                this.setState({
                    infoText: "We cannot find the city you are looking for. Please try another city.",
                });
            } else {
                this.setState({ infoText: "" });
            }
        });
    }
    handleItemClicked = (value, lat, lon) => {
        this.setState({ query: value, suggestions: [] });
        this.props.updateEvents(lat, lon);
        localStorage.setItem('lastCity', JSON.stringify(value));
    };

    componentDidMount() {
        let value = JSON.parse(localStorage.getItem('lastCity'));
        if (value) {
            this.setState({ query: value });
        } else {
            this.setState({ query: "" });
        }
    }

    render() {
        return (
            <div className="CitySearch">
                <WarningAlert text={this.state.warningText} />
                <input type="text" className="city"
                    value={this.state.query} onChange={this.handleInputChanged} placeholder="Search a city..." />
                <InfoAlert text={this.state.infoText} />
                <ul className="suggestions">
                    {this.state.suggestions.map(item =>
                        <li key={item.name_string} onClick={() => this.handleItemClicked(item.name_string, item.lat, item.lon)}>{item.name_string}</li>
                    )}
                </ul>
            </div>
        );
    }
}

export default CitySearch;